import React, { useRef, useState } from 'react';
import CardContent from "@mui/material/CardContent";
import Card from "@mui/material/Card";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";

import { styled } from "@mui/system";
import { FaCopy} from 'react-icons/fa';



const CardWrapper = styled(Card)({
  // background: "#022e6b",
  background: "#FECA70",
  marginBottom: 24,
  marginRight: 5,
  marginLeft: 5,
});


const Input = styled("input")(({ theme }) => ({
  fontSize: 15,
  fontWeight: 300,
  padding: "10px 12px",
  borderRadius: 5,
  border: "none",
  background: "white",
  width: "100%",
  outline: "none",
  textAlign: "center",
  color: "#AF1A25",
}));

export default function ReferralLink({ address }) {

  const copyicon = <FaCopy />;
  const link = `${window.origin}?ref=${address}`;

  const [copySuccess, setCopySuccess] = useState('');
  const textAreaRef = useRef(null);

  function copyToClipboard(e) {
    textAreaRef.current.select();
    document.execCommand('copy');
    // This is just personal preference.
    // I prefer to not show the the whole text area selected.
    e.target.focus();
    setCopySuccess('Copied!');
  };



  return (
    <CardWrapper>
      <CardContent style={{ paddingLeft: 8, paddingRight: 8 }}>
        <Typography style={{color: "#AF1A25"}} gutterBottom variant="h5" textAlign="center">
          Referral Link
        </Typography>
        <Input  ref={textAreaRef} style={{color: "#AF1A25"}} value={address ? link : ""} readOnly />

       <Typography
         
          textAlign="center"
          variant="body2"
          marginTop={2}
          paddingX={3}
    
        >
        <Button 

         variant="contained"
         fullWidth
         marginTop={20}
        //  color="secondary"
         style={{color: "#FFF2E0", background: "#AF1A25"}}

         onClick={copyToClipboard} > {copyicon} Copy
         </Button>  {copySuccess}
       </Typography>


        <Typography
          textAlign="center"
          variant="body2"
          marginTop={2}
          paddingX={3}
          style={{color: "#AF1A25"}}
        >
          Earn upto 19% Referral bonus + Rebate from your referrals by
          sharing the link.
        </Typography>
         

      </CardContent>
    </CardWrapper>
  );
}
