const Home = lazy(() => import('./Home/cards'))
const Referral = lazy(() => import('./Home/referral'))
const ActiveReferral = lazy(() => import('./Home/activereferral'))
const TopReferrer = lazy(() => import('./Home/topreferrer'))
const INFO = lazy(() => import('./Home/info'))



function App() {


  const [isMobile, setIsMobile] = useState(false);


  useEffect(() => {
    function handleResize() {
      if (window.innerWidth <= 768 && !isMobile) {
        setIsMobile(true);
      } else if (window.innerWidth > 768 && isMobile) {
        setIsMobile(false);
        window.location.reload();
      }
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [isMobile]);



  // const { account, connect } = useWallet()
  // useEffect(() => {
  //   if (!account && window.localStorage.getItem('accountStatus')) {
  //     connect('injected')
  //   }
  // }, [account, connect])


  return (
   <Router>
      {/* <Menu> */}

      <MenuBar/>
     
       <div className="App">
       
           <div className="AppGlass">
            <Sidebar/>
            {/* <Connect/> */}
             {/* <MainDash/> */}
            
        <Suspense fallback={<div>Loading...</div>}>
        <Routes>
          <Route exact path="/" element={<Home/>}/>
          <Route exact path="/referral" element={<Referral/>}/>
          <Route exact path="/activeref" element={<ActiveReferral/>}/>
          <Route exact path="/topref" element={<TopReferrer/>}/>
          <Route exact path="/info" element={<INFO/>}/>
          {/* <Route path="*" element={<NotFound/>}/> */}
        </Routes>
        </Suspense>

             <RightSide/>
             
            </div>
        </div>
      {/* </Menu> */}
    </Router>
  );
}

export default App;
